.jobs-container {
    display: flex;
    flex-wrap: wrap;
    color: black;
    gap: 18px;
    padding-bottom: 10rem;

    .jobs-content {
        background: #FFFFFF 0% 0% no-repeat padding-box;
        box-shadow: 0px 3px 6px #557DA526;
        border-radius: 5px;

        .card-body {
            min-height: 200px;
            width: 270px;

            .card-title {
                font-size: 1.7rem;
                margin-bottom: 10px;
            }

            .card-text {
                font-size: 1.4rem;
                opacity: 0.8;
            }
        }

        .footer {

            .location {
                margin-top: 10px;
                position: absolute;
                left: 8px;
                bottom: 23px;

                span {
                    margin-left: 8px;
                }

            }

            button {
                position: absolute;
                right: 8px;
                bottom: 15px;
                width: 125px;
                height: 32px;
                background: #43AFFF33 0% 0% no-repeat padding-box;
                border-radius: 5px;
                border: none;
                color: #303F60;
            }
        }
    }
}

@media (max-width:370px) {
    .jobs-container {
        .jobs-content {
            width: 100%;

            .card-body {
                width: 100%
            }

            button {
                max-width: 8rem;

            }
        }
    }
}


@media (max-width:550px) {
    .jobs-container {
        .jobs-content {
            width: 100%;

            .card-body {
                width: 100%
            }
        }
    }
}